import { useState } from 'react';
import PropTypes from 'prop-types';
import { format } from 'date-fns';
import {
  Box,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Typography
} from '@mui/material';
import MainCard from '../cards/MainCard';
import { Details } from '@mui/icons-material';

export const CustomerListEmails = ({ emails, ...rest }) => {
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(0);

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  return (
    <MainCard {...rest}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              ID
            </TableCell>
            <TableCell>
              Type
            </TableCell>
            <TableCell>
              Content
            </TableCell>
            <TableCell>
              Date
            </TableCell>
            <TableCell>
              Actions
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {emails.map((email) => (
            <TableRow
              hover
              key={email.id}
            // selected={selectedCustomerIds.indexOf(email.id) !== -1}
            >

              <TableCell>
                <Box
                  sx={{
                    alignItems: 'center',
                    display: 'flex'
                  }}
                >
                  <Typography
                    color="textPrimary"
                    variant="body1"
                  >
                    {email.id}
                  </Typography>
                </Box>
              </TableCell>
              <TableCell>
                {email.type}
              </TableCell>
              <TableCell>
                {email.content}
              </TableCell>
              <TableCell>
                {format(Number(email.createdAt), 'dd/MM/yyyy')}
              </TableCell>
              <TableCell>
                <IconButton >
                  <Details />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <TablePagination
        component="div"
        count={emails.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </MainCard>
  );
};

CustomerListEmails.propTypes = {
  emails: PropTypes.array.isRequired
};
